<template>
  <div class="classify">
    <div class="type-box">
      <van-tabs v-model="activeStyle" @click="stylesId">
        <van-tab title="全部" name="-1"></van-tab>
        <van-tab
          v-for="(stylesItem,stylesIndex) in typeData.styles"
          :key="stylesIndex"
          :title="stylesItem.name"
          :name="stylesItem.id"
        ></van-tab>
      </van-tabs>
      <van-collapse v-model="activeNames">
        <van-collapse-item class="collapse-box" title="更多" name="1">
          <van-tabs v-model="activeAreas" @click="areasId">
            <van-tab title="全部" name="-1"></van-tab>
            <van-tab
              v-for="(areasItem,areasIndex) in typeData.areas"
              :key="areasIndex"
              :title="areasItem.name"
              :name="areasItem.id"
            ></van-tab>
          </van-tabs>
          <van-tabs v-model="activeStatus" @click="statusId">
            <van-tab title="全部" name="-1"></van-tab>
            <van-tab
              v-for="(statusItem,statusIndex) in typeData.status"
              :key="statusIndex"
              :title="statusItem.name"
              :name="statusItem.id"
            ></van-tab>
          </van-tabs>
          <van-tabs v-model="activePrices" @click="prices">
            <van-tab title="全部" name="-1"></van-tab>
            <van-tab
              v-for="(pricesItem,pricesIndex) in typeData.prices"
              :key="pricesIndex"
              :title="pricesItem.name"
              :name="pricesItem.id"
            ></van-tab>
          </van-tabs>
          <van-tabs v-model="activeOrders" @click="orders">
            <van-tab
              v-for="(ordersItem,ordersIndex) in typeData.orders"
              :key="ordersIndex"
              :title="ordersItem.name"
              :name="ordersItem.id"
            ></van-tab>
          </van-tabs>
        </van-collapse-item>
      </van-collapse>
    </div>
    <!-- 漫画-内容 -->
    <!-- @scroll="handleScroll($event)" -->
    <div class="cartoon-box" @scroll="handleScroll($event)">
      <!-- <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad"> -->
      <div
        class="cartoon-item"
        v-for="(cartoonItem,cartoonIndex) in cartoonData"
        :key="cartoonIndex"
        @click="goIndetail(cartoonItem.season_id)"
      >
        <div class="auto-img">
          <img v-lazy="cartoonItem.vertical_cover" />
        </div>
        <div class="title">{{cartoonItem.title}}</div>
        <div class="manga-info">
          <span v-if="cartoonItem.is_finish === 1">[完结] 共</span>
          <span v-else>更新至</span>
          <span>{{cartoonItem.last_ord}} 话</span>
        </div>
      </div>
      <!-- </van-list> -->
    </div>
  </div>
</template>

<script>
export default {
  name: "Classify",
  data() {
    return {
      active: 0,

      activeStyle: 0,
      activeAreas: 0,
      activeStatus: -1,
      activePrices: 0,
      activeOrders: 0,
      // 分页
      currentPage: 1,

      activeNames: ["2"],
      // 分类
      typeData: [],

      styleId: -1,
      areaId: -1,
      orderId: 0,
      isFinish: -1,
      isFree: -1,
      // 分页
      pagNum: 30,

      // 漫画数据
      cartoonData: [],
      // 是否显示渲染标签
      isShow: false,

      // 滚动到组件底部
      loading: false,
      finished: false
    };
  },
  created() {
    this.getType();
    this.getCartoonList();
  },
  methods: {
    // 页面滚动事件
    handleScroll(e) {
      const { scrollTop, clientHeight, scrollHeight } = e.target;
      //
      if (scrollTop + clientHeight === scrollHeight) {
        this.pagNum += 30;
        this.getCartoonList();
      }
    },
    //  滚动到组件底部触发
    //  onLoad() {
    //    setTimeout(() => {
    //       // this.pagNum += 41;
    //       for (let i = 0; i < 41; i++) {
    //        this.cartoonData.push(this.cartoonData.length + 1);
    //      }
    //      // 加载状态结束
    //      this.loading = false;
    //      // 数据全部加载完成
    //      if (this.pagNum >= 500) {
    //        this.finished = true;
    //      }
    //    }, 1000);
    //  },
    //  类型筛选
    stylesId(name) {
      this.styleId = name;
      this.getCartoonList();
    },
    //  地区筛选
    areasId(name) {
      this.areaId = name;
      this.getCartoonList();
    },
    //  是否完结
    statusId(name) {
      this.isFinish = name;
      this.getCartoonList();
    },
    //  是否免费
    prices(name) {
      this.isFree = name;
      //
      this.getCartoonList();
    },
    // 人气推荐等筛选
    orders(name) {
      this.orderId = name;
      this.getCartoonList();
    },
    //  获取分类列表
    getType() {
      this.axios({
        method: "get",
        url: "/AllLabel"
      })
        .then(res => {
          //
          this.typeData = res.data.data;
        })
        .catch(err => {
          //
        });
    },
    // 获取漫画列表
    getCartoonList() {
      this.axios({
        method: "get",
        url: `/ClassPage`,
        params: {
          styleId: this.styleId,
          areaId: this.areaId,
          isFinish: this.isFinish,
          order: this.orderId,
          pageNum: this.currentPage,
          pageSize: this.pagNum,
          isFree: this.isFree
        }
      })
        .then(res => {
          this.cartoonData = res.data.data;
          this.isShow = true;
        })
        .catch(err => {});
    },

    //  进入漫画详细
    goIndetail(id) {
      this.$router.push({
        name: "Indetail",
        params: { id: id }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.classify {
  max-width: 545px;
  margin: 0 auto;
  ul {
    margin-bottom: 60px;
  }

  .cartoon-box {
    width: 100%;
    height: 530px;
    overflow-y: scroll;
    display: flex;
    flex-grow: 1;
    flex-wrap: wrap;
    align-items: flex-end;
    &::-webkit-scrollbar {
      width: 10px;
      height: 10px;
    }
    &::-webkit-scrollbar-thumb {
      border-radius: 5px;
      background: #cecece;
    }
    &::-webkit-scrollbar-track {
      background: #fff;
    }
    .cartoon-item {
      width: calc(100% / 3);
      padding: 5px;
      box-sizing: border-box;
      margin-bottom: 5px;
      &:nth-child(3n) {
        margin-right: 0;
      }
      .auto-img {
        margin-bottom: 5px;
        img {
          width: 100%;
        }
      }
      .title {
        width: 100%;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        margin: 5px 0;
      }
      .manga-info {
        font-size: 6px;
        color: #999;
      }
    }
  }
  ::v-deep .van-list__loading {
    margin: 0 auto;
  }
  ::v-deep .van-tabs--line .van-tabs__wrap {
    padding: 0 10px;
  }
  ::v-deep .van-tabs__nav--line.van-tabs__nav--complete {
    padding-left: 0px;
  }
  ::v-deep .van-tabs__wrap--scrollable .van-tab {
    padding: 0 6px;
  }
  ::v-deep .van-collapse-item__content {
    padding-right: 5px;
    padding-bottom: 10px;
  }
  ::v-deep .van-collapse-item__content {
    padding: 0;
  }
  ::v-deep van-tab van-tab--active {
    padding: 0;
  }
  ::v-deep .van-tab {
    flex: none;
    justify-content: normal;
    padding-right: 10px;
  }
}
</style>